export default {
  data() {
    return {
      ruleForm: {
        cost: "",
        max: "",
        min: "",
        sit_coins_limit: "",
        min_banker_coins: "",
        min_bet: "",
        person_limit: "",
        spade_limit: "",
        heart_limit: "",
        club_limit: "",
        diamond_limit: "",
        all_spade_limit: "",
        all_heart_limit: "",
        all_club_limit: "",
        all_diamond_limit: "",
        type_id: "",
        name: "",
        open_game: "",
        open_robot: "",
        robot_type: "",
        is_hundred_game: ""
      },
      rules: {
        cost: [{ required: true, message: "请填写活动形式", trigger: "blur" }],
        max: [{ required: true, message: "请填写活动形式", trigger: "blur" }],
        min: [{ required: true, message: "请填写活动形式", trigger: "blur" }],
        sit_coins_limit: [
          { required: true, message: "请填写活动形式", trigger: "blur" }
        ],
        min_banker_coins: [
          { required: true, message: "请填写活动形式", trigger: "blur" }
        ],
        min_bet: [
          { required: true, message: "请填写活动形式", trigger: "blur" }
        ],
        person_limit: [
          { required: true, message: "请填写活动形式", trigger: "blur" }
        ],
        spade_limit: [
          { required: true, message: "请填写活动形式", trigger: "blur" }
        ],
        heart_limit: [
          { required: true, message: "请填写活动形式", trigger: "blur" }
        ],
        club_limit: [
          { required: true, message: "请填写活动形式", trigger: "blur" }
        ],
        diamond_limit: [
          { required: true, message: "请填写活动形式", trigger: "blur" }
        ],
        all_spade_limit: [
          { required: true, message: "请填写活动形式", trigger: "blur" }
        ],
        all_heart_limit: [
          { required: true, message: "请填写活动形式", trigger: "blur" }
        ],
        all_club_limit: [
          { required: true, message: "请填写活动形式", trigger: "blur" }
        ],
        all_diamond_limit: [
          { required: true, message: "请填写活动形式", trigger: "blur" }
        ],
        type_id: [
          { required: true, message: "请填写活动形式", trigger: "blur" }
        ],
        open_game: [
          { required: true, message: "请填写活动形式", trigger: "blur" }
        ],
        open_robot: [
          { required: true, message: "请填写活动形式", trigger: "blur" }
        ]
      },
      activeName: "",
      id: 0,
      keys: "",
      loading: false,
      //所有房间的数据
      allData: "",
      //匹配当前游戏的条件
      namelist: ["200100", "200101", "200102"],
      //当前游戏的所有数据
      currentlist: {}
    };
  },

  async created() {
    //获取数据
    let { data } = await this.$http.HallFunConfig.GetServerConfig({
      key: "roomdata.lua"
    });
    // console.log(data);
    this.id = data.data[0].id;
    this.keys = data.data[0].sys_key;
    let res = JSON.parse(data.data[0].sys_val);
    // console.log(res);
    this.allData = res;

    this.namelist.forEach((it, index) => {
      Object.keys(res).forEach(item => {
        if (item === it) {
          this.currentlist[item] = res[item];
        }
        if (index === 0) {
          this.ruleForm = res[it];
          this.activeName = it;
        }
      });
    });
    // console.log(this.ruleForm, this.currentlist);
  },

  methods: {
    //tab栏切换
    handleClick(tab) {
      // console.log(tab.name);
      this.namelist.forEach(item => {
        if (item === tab.name) {
          this.ruleForm = this.currentlist[item];
        }
      });
    },

    //保存和发送服务器配置
    submit(formName, type) {
      this.$refs[formName].validate(async valid => {
        if (valid) {
          //准备要发送的数据

          Object.keys(this.currentlist).forEach(item => {
            if (item === this.ruleForm.type_id) {
              this.currentlist[item] = this.ruleForm;
            }
          });
          // console.log(this.ruleForm, this.currentlist);

          Object.keys(this.currentlist).forEach(item => {
            Object.keys(this.allData).forEach(it => {
              if (item === it) {
                this.allData[it] = this.currentlist[item];
              }
            });
          });
          // console.log(this.allData, this.currentlist);

          //判断type类型
          if (type === 1) {
            //发送put
            let { data } = await this.$http.HallFunConfig.PutServerConfig({
              keys: this.keys,
              values: JSON.stringify(this.allData),
              id: this.id
            });
            // console.log(data);
            if (data.code === 1 && data.msg === "ok") {
              this.$message({
                type: "success",
                message: "保存成功!"
              });
            }
          } else if (type === 2) {
            //发送post
            this.loading = true;

            let { data } = await this.$http.HallFunConfig.PostServerConfig({
              keys: this.keys,
              values: JSON.stringify(this.allData),
              id: this.id
            });
            // console.log(data);
            if (data.code === 1 && data.msg === "ok") {
              this.loading = false;
              this.$message({
                type: "success",
                message: "发送服务器配置成功!"
              });
            }
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    }
  }
};



